import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'diccionario'
})
export class DiccionarioPipe implements PipeTransform {

  transform(value: any, opc: boolean): any {
      opc = true;

      if(opc == true){
        value=value.replace(/CH39R/g,"'");
        value=value.replace(/CH34R/g,'"');
        value=value.replace(/CH35R/g,'#');
        value=value.replace(/CH44R/g,',');
        value=value.replace(/CH10R/g,' ');
        value=value.replace(/CH176R/g,'&deg;');	
        value=value.replace(/CH8226R/g,'');
        value=value.replace(/CH9R/g,' ');	
        value=value.replace(/CH180R/g,'');	
        value=value.replace(/CH58R/g,':');	
      
        value=value.replace(/&aacute;/g,'');
        value=value.replace(/&eacute;/g,'');
        value=value.replace(/&iacute;/g,'');
        value=value.replace(/&oacute;/g,'');
        value=value.replace(/&uacute;/g,'');
        value=value.replace(/&Aacute;/g,'');
        value=value.replace(/&Eacute;/g,'');
        value=value.replace(/&Iacute;/g,'');
        value=value.replace(/&Oacute;/g,'');
        value=value.replace(/&Uacute;/g,'');
        value=value.replace(/&ntilde;/g,'');
        value=value.replace(/&Ntilde;/g,'');
      
        value=value.replace(/CH225R/g,'');
        value=value.replace(/CH233R/g,'');
        value=value.replace(/CH237R/g,'');
        value=value.replace(/CH243R/g,'');
        value=value.replace(/CH250R/g,'');
        value=value.replace(/CH193R/g,'');
        value=value.replace(/CH201R/g,'');
        value=value.replace(/CH205R/g,'');
        value=value.replace(/CH211R/g,'');
        value=value.replace(/CH2018R/g,'');
        value=value.replace(/CH241R/g,'');
        value=value.replace(/CH209R/g,'');
        value=value.replace(/CH207R/g,'');
        value=value.replace(/CH239R/g,'');
        value=value.replace(/CH252R/g,'');
        value=value.replace(/CH220R/g,'');	
        value=value.replace(/CH92R/g,'&#92;');
        value=value.replace(/CH145R/g,'&lsquo;');
        value=value.replace(/CH146R/g,'&rsquo;');
      }

    return `${value}`;
  }

}



